.App {
  margin: 0%;
  display: grid;
  background: linear-gradient(90deg, #222222 20px, transparent 1%) center, linear-gradient(#222222 20px, transparent 1%) center, #333333;
  background-size: 22px 22px;
  grid-template-rows: 50px auto;
  grid-template-columns: 500px auto;
  grid-template-areas:
    'leftbar card'
    'leftbar card';
}

@media screen and (max-width: 992px) {
  .App {
    margin: 0%;
    display: grid;
    grid-template-rows: 400px auto;
    grid-template-columns: auto;
    grid-template-areas:
      'topbar'
      'card';
  }
}

@media screen and (max-width: 362px) {
  .App {
    margin: 0%;
    display: grid;
    grid-template-rows: 250px auto;
    grid-template-columns: auto;
    grid-template-areas:
      'topbar'
      'card';
  }
}

@media screen and (max-width: 300px) {
  .App {
    margin: 0%;
    display: grid;
    grid-template-rows: 300px auto;
    grid-template-columns: auto;
    grid-template-areas:
      'topbar'
      'card';
  }
}