.ProjectCard {
  position: relative;
  background-color: white;
  height: auto;
  width: 450px;
  max-height: 600px;
  margin: auto;
  padding: 24px;
  padding-bottom: 42px;
  overflow: scroll;
}

.ProjectCard img {
  position: absolute;
  padding: 24px;
  right: 0;
  top: 0;
}

@media screen and (max-width: 600px) {
  .ProjectCard {
    position: relative;
    width: 80%;
  }
}

@media screen and (max-height: 700px) {
  .ProjectCard {
    position: relative;
    max-height: 400px;
  }
}
