html {
  height: 100vh;
}

@font-face {
  font-family: "Brix Sans";
  src: url("./assets/fonts/Brix-Sans-Regular.otf") format("opentype"),
       url("./assets/fonts/BrixSansRegular.ttf") format("ttf"),
       url("./assets/fonts/BrixSansRegular.woff2") format("woff2"),
       url("./assets/fonts/BrixSansRegular.woff") format("woff");
}

* {
  font-family: "Brix Sans", sans-serif;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  min-height: 100vh;
}
