h1 {
  margin: 18px 0;
}

.Projects {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.Projects .Title {
  font-size: 1.375rem;
  margin-bottom: 5px;
}

.Projects .Title:hover {
  color: #d22136;
}

.Projects p {
  font-size: 1.125rem;
}

.ProjectFeature h5 {
  text-transform: uppercase;
  color: #aaaaaa;
  margin-top: 10px;
}

.ProjectFeature h5:hover {
  color: #d22136;
}

.ProjectFeature p {
  font-size: 1.05rem;
}

.Projects a {
  text-decoration: none;
  color: black;
}
