.LeftBar {
  grid-area: leftbar;
  /* color: black; */
  color: #cccccc;
  /* background-color: #222222; */
  /* background-color: #111517; */
  /* background: linear-gradient(90deg, #222222 20px, transparent 1%) center, linear-gradient(#222222 20px, transparent 1%) center, #333333;
  background-size: 22px 22px; */
  

  height: 100vh;
  /* box-shadow: 0.5rem 0 3rem #000; */
  /* -webkit-box-shadow: 10px 10px 93px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 93px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 93px 0px rgba(0, 0, 0, 0.75); */
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.LeftBar h1 {
  /* text-transform: uppercase; */
  font-size: 2.25rem;
  padding: 40px;
  margin-top: 100px;
}

.LeftBar ul {
  /* float: bottom;
  list-style: none;
  margin-left: 20px; */
  list-style: none;
  display: flex;
  flex-flow: row;
  margin-left: 20px;
  margin-bottom: 10px;
}

.LeftBar ul li {
  margin: 20px;
}

.LeftBar a,
a:active,
a:visited {
  text-decoration: none;
  /* color: white; */
  color: black;
}

.LeftBar a:hover {
  color: #D22136;
}

@media screen and (max-width: 992px) {
  .LeftBar {
    grid-area: topbar;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .LeftBar h1 {
    margin: 40px;
  }

  .LeftBar ul {
    list-style: none;
    display: flex;
    flex-flow: row;
    margin-left: 60px; /* h1 margin + h1 padding - li margin */
  }

  .LeftBar ul li {
    margin: 20px;
  }
}

@media screen and (max-width: 460px) {
  .LeftBar h1 {
    margin: 40px 10px;
  }

  .LeftBar ul {
    margin-left: 40px; /* h1 margin + h1 padding - li margin */
  }

  .LeftBar ul li {
    margin: 10px;
  }
}

@media screen and (max-width: 402px) {
  .LeftBar h1 {
    font-size: 2rem;
    margin: 10px;
  }

  .LeftBar ul {
    margin-left: 40px; /* h1 margin + h1 padding - li margin */
  }

  .LeftBar ul li {
    margin: 10px;
  }
}

@media screen and (max-width: 362px) {
  .LeftBar h1 {
    font-size: 1.5rem;
    margin: 10px;
  }

  .LeftBar ul {
    margin-left: 40px; /* h1 margin + h1 padding - li margin */
  }

  .LeftBar ul li {
    margin: 10px;
    font-size: 0.8rem;
  }
}
